import vineshard from '../../img/vineyard.jpg'

const Vintus=()=>{
    return(
        <section className="Vintus_background">
        <div className="Vintus">
          <article className="Vintus_img">
            <img src={vineshard} alt=""/>
          </article>
          <article className="Vintus_article">
            <h2>Vintus | Fundada en 1998</h2>
            <p>Situados entre las colinas ondulantes y los viedos, en Vinoteca Vintus creamos vinos excepcionales que capturan la esencia de nuestro venerado terruo. Con el impresionante teln de fondo del pintoresco paisaje rural, curamos con pasin y nutrimos la misma tierra que ha sido nuestro lugar de nacimiento durante ms de dos dcadas.

              Explorando el arte de la vinificacin, abrazamos cada cosecha como una oportunidad para crear algo extraordinario. Desde el momento en que se recogen las uvas de las vides hasta el cuidadoso proceso de aejamiento, nuestra dedicacin a la calidad brilla en cada botella que producimos.
              
              nete a nosotros en un viaje de sabor y tradicin mientras continuamos escribiendo nuestra historia, cosecha tras cosecha. Vinoteca Vintus: Un brindis por la belleza del tiempo, el lugar y los exquisitos sabores que nos definen.
              
              </p>
              <div className="corner_top"></div>
              <div className="corner_botom"></div>
          </article>
          </div>
        </section>
    )
}
export default Vintus