
import { Link } from 'react-router-dom';
import { useState } from 'react';
import useUser from '../../../Hooks/useUser';


const LogInForm = ()=>{
    const [email,setEmail]=useState('')
    const [pass,setPass]=useState('')
    const {log} = useUser("Entrando...","Bienvenido a Vintus","Email o contrasea incorrecta")


    const submitHandler = (event) => {
        event.preventDefault();
        console.log('a')

        log({email:email,password:pass},`${process.env.REACT_APP_API_URL}/Vintus/Users/Login`);
   
            
         };
    return(
                    <form action="" onSubmit={submitHandler}>
                        <h2>Vintus</h2>
                        <p>Bienvenido a Vintus logee se con su usuario y contrasea</p>
                        <input type="text" id="email" className="form__input" required minLength="3" value={email} placeholder="EMAIL" onChange={(event)=>{setEmail(event.target.value)}}/>
                        < p id="Email" className="animated"></p>
                        <input type="password" id="Pass" className="form__input" required minLength="3" value={pass} placeholder="CONTRASEA" onChange={(event)=>{setPass(event.target.value)}}/>
                        < p id="Password" className="animated"></p>
                        <div>
                        <Link to='/'>Cambiar contrasea</Link>
                        <span>/</span>
                        <Link to='/CrearCuenta'>Crear un usuario</Link>
                        </div>
                        <button>Ingresar</button>

                    </form>
    )
}

export default LogInForm